import { FC, PropsWithChildren } from "react";
import ColBox from "src/components/ColBox";

const WrapperBox: FC<{
  name: string;
  bankName: string;
  bankNumber: string;
  kakaoPayLink?: string;
}> = (props) => {
  const { name, bankName, bankNumber, kakaoPayLink } = props;

  const handleKakaoPayClick = () => {
    window.open(kakaoPayLink);
  };

  const handleCopyClick = () => {
    navigator.clipboard.writeText(`${name}\n ${bankName} ${bankNumber}`);
    alert("계좌 정보가 복사되었습니다.");
  };

  return (
    <div className="flex gap-1 justify-between">
      <div className="flex flex-col gap-2 align-center place-self-center">
        <p>
          {bankName} {bankNumber}
        </p>
        <p>{name}</p>
      </div>
      <div className="flex flex-col justify-start">
        <button
          className="text-sm bg-primary text-white rounded-xl p-1 w-16 m-1"
          onClick={handleCopyClick}
        >
          복사하기
        </button>
        {kakaoPayLink && (
          <button
            className="text-sm bg-yellow-300 rounded-xl p-1 w-16 m-1 mr-0"
            onClick={handleKakaoPayClick}
          >
            카카오페이
          </button>
        )}
      </div>
    </div>
  );
};

const Card: FC<PropsWithChildren<{ title: string }>> = ({
  title,
  children,
}) => {
  return (
    <div className="w-65 bg-secondary p-5 rounded-xl flex flex-col gap-4">
      <p className="pb-2 border-b-2 border-primaryLight">{title}</p>
      {children}
    </div>
  );
};

