import { FC, PropsWithChildren, useRef } from "react";
import { Modal } from "src/components/Modal";
import ImageGallery from "react-image-gallery";
import { useMatch, useNavigate } from "react-router-dom";
import ReactImageGallery from "react-image-gallery";

export type GalleryProps = PropsWithChildren<{}>;

const _images = Array.from({ length: 20 }).map((_, idx) => ({
  original: `${process.env.PUBLIC_URL}/imgs/g-${idx + 1}.jpg`,
  thumbnail: `${process.env.PUBLIC_URL}/imgs/g-t-${idx + 1}.jpg`,
}));

const FullScreenIcon = () => {
  return (
    <svg
      width="75px"
      height="75px"
      viewBox="0 0 48 48"
      xmlns="http://www.w3.org/2000/svg"
      className="absolute mx-auto my-auto inset-0 text-white "
      fill="currentColor"
    >
      <path d="M24,48A24,24,0,1,1,48,24,24,24,0,0,1,24,48ZM24,1A23,23,0,1,0,47,24,23,23,0,0,0,24,1Z" />
      <path d="M31.5,24.5h-15a0.5,0.5,0,0,1,0-1h15A0.5,0.5,0,0,1,31.5,24.5Z" />
      <path d="M24,32a0.5,0.5,0,0,1-.5-0.5v-15a0.5,0.5,0,0,1,1,0v15A0.5,0.5,0,0,1,24,32Z" />
      <rect width="48" height="48" fill="none" />
    </svg>
  );
};

const ImageCard: FC<{ src: string; onClick: () => void }> = ({
  src,
  onClick,
}) => {
  return (
    <div className="w-full m-1 md:m-2 bg-secondary relative">
      <div
        className="absolute w-full h-full hover:bg-primary hover:opacity-50 opacity-0 cursor-pointer rounded-lg"
        onClick={onClick}
      >
        <FullScreenIcon />
      </div>
      <img
        alt="gallery"
        className="block object-scale-down w-full h-full rounded-lg"
        src={src}
      />
    </div>
  );
};

export const Gallery: FC<GalleryProps> = () => {
  const navigate = useNavigate();
  const match = useMatch("gallery");
  const ref = useRef<ReactImageGallery | null>(null);
  const handleImageClick = (idx: number) => {
    navigate("gallery");
    ref.current?.slideToIndex(idx);
  };

  return (
    <section className="overflow-hidden text-gray-700 container mx-auto max-w-5xl">
      <div className="columns-3">
        {_images.map((image, idx) => (
          <div className="w-full object-cover rounded-lg" key={idx}>
            <ImageCard
              src={image.thumbnail + "?" + idx}
              onClick={() => handleImageClick(idx)}
            />
          </div>
        ))}
      </div>
      <Modal open={!!match} onCloseClick={() => navigate("")}>
        <ImageGallery
          ref={ref}
          lazyLoad={true}
          showFullscreenButton={false}
          showPlayButton={false}
          items={_images}
          additionalClass="sm:p-10 h-screen max-h-screen w-full"
        />
      </Modal>
    </section>
  );
};
