import { Map, MapMarker as KakaoMapMarker } from "react-kakao-maps-sdk";
import { FC, MouseEventHandler, useState } from "react";

const LONGITUDE = 127.0725;
const LATITUDE = 37.2535;
const LAT_LONG = { lat: LATITUDE, lng: LONGITUDE };
const POSITION_NAME = "더 마레보";
const POSITION_LOCATION_NAME =
  "경기 수원시 영통구 봉영로 1591 더마레보호텔&웨딩";

const NaviButton: FC<{
  title: string;
  iconUrl: string;
  onClick?: MouseEventHandler<HTMLButtonElement>;
}> = ({ title, iconUrl, onClick }) => {
  return (
    <button
      className="rounded-lg bg-backgroundSecondary hover:bg-secondary px-2 py-2 grid grid-flow-row justify-items-center"
      onClick={onClick}
    >
      <img
        src={iconUrl}
        alt={title}
        width="50px"
        height="50px"
        className="rounded-full "
      />
      <p className="text-xs sm:text-lg mt-2 text-textSecondary align-middle">
        {title}
      </p>
    </button>
  );
};

const KakaoMap: FC = () => {
  const [canDrag, setCanDrag] = useState(false);
  const handleKakaoNaviClick = () => {
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    Kakao.Navi.start({
      name: POSITION_NAME,
      x: LONGITUDE,
      y: LATITUDE,
      coordType: "wgs84",
    });
  };
  const handleKakaoMapClick = () => {
    window.open(
      `https://map.kakao.com/link/to/${POSITION_NAME},${LATITUDE},${LONGITUDE}`
    );
  };

  const handleNaverMapClick = () => {
    window.open(
      `http://m.map.naver.com/route.nhn?menu=route&ename=${POSITION_NAME}&ex=${LONGITUDE}&ey=${LATITUDE}&pathType=0&showMap=true`
    );
  };

  const handleTMapNaviClick = () => {
    window.open(`
    https://surl.tmap.co.kr/cc15a360`);
  };

  const handleCopyClick = () => {
    navigator.clipboard.writeText(POSITION_LOCATION_NAME);
    alert("주소가 복사되었습니다.");
  };

  return (
    <div className="grid grid-flow-row gap-2 grow">
      <Map
        className="w-full h-[420px]"
        center={LAT_LONG}
        level={4}
        draggable={canDrag}
        onClick={() => setCanDrag(true)}
      >
        <KakaoMapMarker
          position={LAT_LONG}
          image={{
            src: `${process.env.PUBLIC_URL}/imgs/marevo.png`,
            size: {
              width: 80,
              height: 80,
            },
          }}
        ></KakaoMapMarker>
      </Map>
      <button
        onClick={handleCopyClick}
        className="rounded-lg bg-backgroundSecondary hover:bg-secondary px-2 py-2 flex justify-center"
      >
        <p className="text-textSecondary self-center pr-2">
          경기 수원시 영통구 봉영로 1591 더마레보호텔
        </p>
        <div className="text-sm bg-primary text-white rounded-xl p-1 w-16 m-1">
          복사하기
        </div>
      </button>
      <div className="grid grid-cols-4 gap-2">
        <NaviButton
          title="카카오맵"
          iconUrl={`${process.env.PUBLIC_URL}/imgs/kakao-map.png`}
          onClick={handleKakaoMapClick}
        />
        <NaviButton
          title="네이버맵"
          iconUrl={`${process.env.PUBLIC_URL}/imgs/naver-map.png`}
          onClick={handleNaverMapClick}
        />
        <NaviButton
          title="카카오내비"
          iconUrl={`${process.env.PUBLIC_URL}/imgs/kakao-navi.png`}
          onClick={handleKakaoNaviClick}
        />
        <NaviButton
          title="티맵"
          iconUrl={`${process.env.PUBLIC_URL}/imgs/t-map.png`}
          onClick={handleTMapNaviClick}
        />
      </div>
    </div>
  );
};

export default KakaoMap;
