import clsx from "clsx";
import { FC, PropsWithChildren, useEffect } from "react";

export const Modal: FC<
  PropsWithChildren<{
    open: boolean;
    onCloseClick?: () => void;
  }>
> = (props) => {
  const { children, open, onCloseClick } = props;

  useEffect(() => {
    const clearFunc = () => {
      document.body.style.overflow = "unset";
    };

    const openFunc = () => {
      document.body.style.overflow = "hidden";
    };

    if (open) {
      openFunc();
    } else {
      clearFunc();
    }
    () => clearFunc();
  }, [open]);

  return (
    <div
      className={clsx(
        { "fade-in": open, fade: !open },
        "bg-backgroundSecondary fixed top-0 left-0 w-full h-full outline-none overflow-x-hidden overflow-y-auto"
      )}
      aria-modal="true"
      role="dialog"
    >
      <div className="relative">
        <div
          className="absolute top-2 right-2 text-primary cursor-pointer z-10"
          onClick={onCloseClick}
        >
          <svg
            width="20px"
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 12 12"
          >
            <line
              x1="1"
              y1="11"
              x2="11"
              y2="1"
              stroke="currentColor"
              strokeWidth="2"
            />
            <line
              x1="1"
              y1="1"
              x2="11"
              y2="11"
              stroke="currentColor"
              strokeWidth="2"
            />
          </svg>
        </div>
        <div className="absolute inset-y-1/2 w-full">{children}</div>
      </div>
    </div>
  );
};
