import { FC, PropsWithChildren, useEffect, useState } from "react";
import { Heart } from "src/components/Heart";
import ColBox from "../components/ColBox";

export type TimerProps = PropsWithChildren<{}>;

const TICK_MAX = 1000;
const SEC_MAX = 60;
const MIN_MAX = 60;
const HOUR_MAX = 24;

const Pad = (num: number) => (num > 9 ? num.toString() : "0" + num);

export const Timer: FC<TimerProps> = (props) => {
  const [time, setTime] = useState(0);

  useEffect(() => {
    const interval = setInterval(() => {
      const date = Math.floor(
        new Date("2023-02-25T13:30:00").getTime() -
          new Date().getTime() +
          new Date().getTimezoneOffset()
      );

      setTime(date);
    }, 1000);

    return () => clearInterval(interval);
  }, []);

  const days = Math.floor(time / (TICK_MAX * SEC_MAX * MIN_MAX * HOUR_MAX));
  const hours = Math.floor(
    (time % (TICK_MAX * SEC_MAX * MIN_MAX * HOUR_MAX)) /
      (TICK_MAX * SEC_MAX * MIN_MAX)
  );
  const min = Math.floor(
    (time % (TICK_MAX * SEC_MAX * MIN_MAX)) / (TICK_MAX * SEC_MAX)
  );
  const sec = Math.floor((time % (TICK_MAX * SEC_MAX)) / TICK_MAX);

  return (
    <ColBox className="text-center">
      <div className="flex items-center text-sm xs:text-lg md:text-2xl">
        <p className="pr-1">마성민</p>
        <Heart width={15} height={15} />
        <p className="pl-1">심하늘의 결혼식까지 {days}일 남았습니다.</p>
      </div>

      <p className="text-5xl md:text-7xl">
        {Pad(hours)} : {Pad(min)} : {Pad(sec)}
      </p>
    </ColBox>
  );
};
