import { FC, PropsWithChildren } from "react";
import clsx from "clsx";
export type ColBoxProps = { className?: string };

const ColBox: FC<PropsWithChildren<ColBoxProps>> = ({
  children,
  className,
}) => {
  return (
    <div className={clsx("grid grid-cols-1 gap-14", className)}>{children}</div>
  );
};

export default ColBox;
