import { FC, PropsWithChildren } from "react";
import clsx from "clsx";
import ColBox from "./ColBox";
import ScrollAnimationItem from "react-scroll-fade-animation";

export type PaperProps = {
  variant?: "primary" | "secondary";
  title?: string;
  offsetHeight?: number;
  colBoxClassName?: string;
};

const Paper: FC<PropsWithChildren<PaperProps>> = (props) => {
  const {
    variant = "primary",
    colBoxClassName,
    title,
    children,
    offsetHeight = 100,
  } = props;

  return (
    <ScrollAnimationItem duration={1000} path="top" offsetHeight={offsetHeight}>
      <div
        className={clsx("flex justify-center px-5 py-20 snap-center", {
          ["bg-backgroundSecondary"]: variant === "secondary",
        })}
      >
        <ColBox className={colBoxClassName}>
          {title && (
            <div className="flex">
              <div className="flex-auto"></div>
              <div className="flex justify-center flex-none border-y-2  border-y-primary shrink-0">
                <span className="text-primary font-bold text-center text-2xl tracking-widest -mr-[0.2em]">
                  {title}
                </span>
              </div>
              <div className="flex-auto"></div>
            </div>
          )}
          {children}
        </ColBox>
      </div>
    </ScrollAnimationItem>
  );
};

export default Paper;
